<template>
    <div class="cates_container">
        <!-- 左侧菜单 -->
        <left-menu  class="left_menu" 
        :left-menu-list="leftMenuList"
        @handleItemTap="handleItemTap"/>
        <!-- 右侧商品内容 -->
        <right-content class="right_content" :right-content="rightContent"/>
    </div>
</template>

<script>
//import x from ''

import LeftMenu from './LeftMenu'
import RightContent from './RightContent'

import { getCategories } from 'network/category'
export default {
    name: 'CatesContainer',
    props: {},
    data () {
        return {
            leftMenuList: [],
            rightContent: [],
            Cates: []
        }
    },
    components: {
        LeftMenu,
        RightContent
    },
    created() {
        this.getCategories()
    },
    methods: {
        // 网络请求相关方法
        getCategories() {
            getCategories().then(res => {
                this.Cates = res.message;
                this.rightContent = this.Cates[0].children;
                this.leftMenuList = this.Cates.map(item => item.cat_name);
            })
        },
        handleItemTap(index){
            this.rightContent = this.Cates[index].children;
        }
    },
}
</script>

<style scoped>
.cates_container{
    height: calc( 100vh - 62px - 44px );
    display: flex;
}
.left_menu{
    flex:2;

}
.right_content{
    flex:5;
}
</style>
