<template>
    <div class="left_menu">
        <Scroll class="scroll_content_left">
            <div v-for="(item, index) in leftMenuList" 
            :key="index" 
            class="menu_item"
            :class="{active:currentIndex===index}"
            @click="handleItemTap(index)">
                {{item}}
            </div>
        </Scroll>
    </div>
</template>

<script>
//import x from ''
import Scroll from 'components/common/scroll/Scroll'
export default {
    name: 'LeftMenu',
    props: {
        leftMenuList: {
            type: Array,
            default(){
                return []
            }
        }
    },
    data () {
        return {
            currentIndex: 0
        }
    },
    components: {
        Scroll
    },
    computed: {},
    watch: {},
    methods: {},
    beforeMount () { },
    methods: {
        handleItemTap(index){
            this.currentIndex=index;
            this.$emit('handleItemTap',index)
        }
    }, 
    activated () { }
}
</script>

<style scoped>
.scroll_content_left{
    overflow: hidden;
    height: 100%;
}
.menu_item{
    height: 30px;
    margin: 5px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px
}
.active{
    color: var(--color-tint);
    border-left: 5px solid currentColor;
}
</style>
